/* products */
.products-box{
    width: 100%;
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    flex-wrap: wrap;
    margin-top: 50px;
  }
  
  .product{
    width: 350px;
    height: auto; 
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    margin-bottom: 50px;
    margin-right: 24px;
    transition: all 0.3s ease-in-out;
    padding: 12px;
    border-radius: 10px; 
  }
  
  .product:hover {
    box-shadow: 5px 5px 20px 0px rgba(0, 0, 0, 0.1);
  }
  
  @media(min-width: 768px){
    .product{
      width: 300px;
    }
  }
  
  .product .product-img{
    width: 180px;
    height: 215px;  
  }
  
  .product .product-img img{
    width: 100%;
    height: 100%;
  }
  
  .product .product-text{
    margin-bottom: 10px;
    font-size: 16px;
    text-align: center;  
  }
  
  .product .product-text.title{
    color: #e00a02;  
    font-weight: 600;
    font-size: 20px;
    margin-top: 10px;
  }
  
  .product .product-text.description{
    height: 50px;
    overflow: hidden;
  }
  
  .product .product-text.price{
    font-weight: 600;
  }
  
  .product .product-text.cart-price{
    font-weight: 600;
    color: #e00a02;
  }

.sidenav {
    height: 100%;
    width: 0;
    position: fixed;
    z-index: 1;
    top: 0;
    left: 0;
    background-color: #111;
    overflow-x: hidden;
    transition: 0.5s;
    padding-top: 60px;
  }
  
  .sidenav a {
    padding: 8px 8px 8px 32px;
    text-decoration: none;
    font-size: 25px;
    color: #818181;
    display: block;
    transition: 0.3s;
  }
  
  .sidenav a:hover {
    color: #f1f1f1;
  }
  
  .sidenav .closebtn {
    position: absolute;
    top: 0;
    right: 25px;
    font-size: 36px;
    margin-left: 50px;
  }
  
  @media screen and (max-height: 450px) {
    .sidenav {padding-top: 15px;}
    .sidenav a {font-size: 13px;}
  }

